@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=IBM+Plex+Mono|IBM+Plex+Sans|Paytone+One");
/* BASE */
html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: "IBM Plex Mono", monospace;
  font-size: 16px;
  margin: 0;
  padding: 0;
  overflow-x: hidden; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Paytone One", sans-serif; }

/* NAVEGACIÓN */
.bg-nav {
  width: 100%;
  height: 65px;
  background: #5f2c82;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #49a09d, #5f2c82);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #49a09d, #5f2c82);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ }

.navbar-brand {
  display: inline-block;
  padding-top: 0.3125rem;
  padding-bottom: 0.3125rem;
  margin-right: 1rem;
  font-size: 1.25rem;
  line-height: inherit;
  white-space: nowrap; }

.navbar-brand .img-fluid {
  width: 100%;
  max-height: 32px; }

.navbar-nav .nav-link {
  color: #fff;
  text-transform: uppercase; }

.navbar .dropdown-item {
  color: #424242; }

.navbar-toggler-icon {
  color: #fff;
  margin-top: 8px; }

/* FOOTER */
.wrapper-footer {
  padding: 20px 0;
  background-color: #3a539b;
  color: rgba(255, 255, 255, 0.6); }

.flex-block-wrapper {
  display: flex;
  justify-content: center; }

​
.centered-flex-block {
  z-index: 2;
  opacity: 1 !important;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  max-width: 30%;
  text-align: center; }

/* HERO */
.hero-header-container {
  background: url(images/bg-image.jpg) no-repeat center center;
  background-size: cover;
  position: relative;
  padding-bottom: 100px;
  background-blend-mode: overlay;
  background-color: #6c7a89; }

.diagonal-section {
  position: absolute;
  background-color: #fff;
  width: 110%;
  bottom: -5%;
  left: -5%;
  transform: rotate(-2.5deg);
  height: 150px; }

.hero-header-content {
  display: flex;
  flex-direction: column-reverse;
  height: 1000px; }

.hero-header-left {
  display: flex;
  order: 1;
  width: 100%;
  height: 500px;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  /* background-color: $black; */ }

.hero-header-left h1,
.hero-header-left {
  color: #FFF; }

.aligner {
  display: flex;
  justify-content: center;
  flex-direction: column;
  max-width: 50%; }

.hero-header-right {
  display: flex;
  order: 2;
  width: 100%;
  height: 500px;
  align-items: center;
  justify-content: center; }

.hero-header-right img {
  margin: 0 auto; }

@media (min-width: 768px) {
  .hero-header-container {
    padding-bottom: 200px; }
  .diagonal-section {
    bottom: -10%;
    height: 150px; }
  .hero-header-left {
    width: 60%;
    background-color: transparent;
    height: 500px; }
  .hero-header-right {
    width: 40%; }
  .hero-header-content {
    display: flex;
    flex-direction: row;
    height: 500px; } }

/* CONTACTO */
.bg-contact {
  background-image: url(images/bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-color: #3a539b;
  background-blend-mode: multiply;
  padding-bottom: 100px; }

.contact-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  margin: 50px auto;
  width: calc(100% -30px);
  color: #fff; }

.contact-content hr {
  width: 50px;
  margin: auto 0 30px 0;
  border-top: 1px dashed rgba(255, 255, 255, 0.2); }

.contact-left {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column; }

.contact-left-info {
  width: 100%;
  background-color: #2a1d39;
  padding: 20px 10% 20px 20px; }

.contact-left-map {
  background-color: #fff;
  width: 100%; }

.contact-right {
  width: 100%; }

.contact-form {
  padding: 20px;
  background-color: #774cfd; }

.contact-form input,
.contact-form form textarea {
  width: 100%;
  border-radius: 4px;
  border: none;
  margin-bottom: 2em;
  color: #acb7ee; }

ul.social-links,
.social-links li {
  margin: 1rem 0 0 0;
  padding: 0; }

.social-links li {
  float: left;
  margin-left: 20px;
  list-style: none;
  font-size: 1.8em; }

.social-links > li:first-child {
  margin-left: 0; }

.social-links a {
  color: rgba(255, 255, 255, 0.2);
  -webkit-transition: ease-in-out 0.5ms;
  transition: ease-in-out 0.5ms; }

.social-links a:hover {
  color: #acb7ee; }

@media (min-width: 768px) {
  .contact-left {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row; }
  .contact-left-info {
    width: 50%; }
  .contact-left-map {
    width: 50%; } }

@media (min-width: 992px) {
  .contact-content {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    margin: 50px 10% 0 10%;
    height: 553px; }
  .contact-left {
    width: 80%; }
  .contact-right {
    width: 20%; } }

@media (min-width: 1200px) {
  .contact-left {
    width: 70%; }
  .contact-right {
    width: 30%; } }

@media (max-width: 575.98px) {
  .contact-left-map {
    display: none; } }

.flex-block-wrapper {
  display: flex;
  justify-content: center; }

.centered-flex-block {
  z-index: 2;
  opacity: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  max-width: 30%;
  text-align: center; }
